import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import styled from 'styled-components'
import '../styles/components/heroBanner.scss'
import scrollTo from 'gatsby-plugin-smoothscroll';

import BackgroundImage from 'gatsby-background-image'

const BackgroundEspecial = ({className, subtitle, hasButton}) => {


    const query = useStaticQuery(graphql`
        query backgroundSpecial {
            file(name: {eq: "heroBg"}) {
                childImageSharp {
                    fluid (quality: 70){
                        ...GatsbyImageSharpFluid
                    }
                }
            }
        }
    `)

    const imageData = query.file.childImageSharp.fluid

    return(

        <BackgroundImage
          Tag="section"
          className={className}
          fluid={imageData}
          backgroundColor={`#040e18`}
        >
            <div className="hero-body">
                <div className="container has-text-centered">
                <h1 className="title">
                    Veja os dados da COVID-19 em <strong>Nova Friburgo</strong>
                </h1>
                <h2 className="subtitle">
                    {subtitle}
                </h2>
                {hasButton?<button className="button" onClick={() => scrollTo('#portfolio')}>See my work</button>:<></>}
                </div>
            </div>
        </BackgroundImage>
    )
    
}

const StyledBackgroundSection = styled(BackgroundEspecial)`
  width: 100%;
  background-position: top center;
  background-repeat: repeat-y;
  background-size: cover;
  margin: 0;
  margin-bottom: 60px;
  padding: 7rem 1.5rem;
`

export default StyledBackgroundSection