import React from "react"
import SEO from "../components/seo"
import Layout from "../components/layout"
import PageTransition from 'gatsby-plugin-page-transitions';
import BackgroundEspecial from '../components/backgroundEspecial';

class CovidPage extends React.PureComponent {


    componentDidMount(){
        let w = window.innerWidth;
        this.initViz(w)
    }

    initViz(w) {  
        
        const casosUrl = 'https://public.tableau.com/views/COVID-19-NF-RJ-Casos/Dashboard1?:language=pt&:display_count=y&:origin=viz_share_link';  
        const casosContainer = this.casosContainer; 

        const obitosUrl = 'https://public.tableau.com/views/COVID-19-NF-RJ-Obitos/Dashboard2?:language=pt&:display_count=y&:origin=viz_share_link';  
        const obitosContainer = this.obitosContainer; 

        const recuperadosUrl = 'https://public.tableau.com/views/COVID-19-NF-RJ-Recuperados/DashRecuperados?:language=pt&:display_count=y&:origin=viz_share_link';  
        const recuperadosContainer = this.recuperadosContainer; 

        const ativosUrl = 'https://public.tableau.com/views/COVID-19-NF-RJ-Ativos/DashAtivos?:language=pt&:display_count=y&:origin=viz_share_link';  
        const ativosContainer = this.ativosContainer; 

        let options;

        if(w>500){
            options = {
                hideTabs: true,
                width: w-10,
                height: 700,
            };
        } else{
            options = {

            };
        }

        new window.tableau.Viz(casosContainer, casosUrl, options)  
        new window.tableau.Viz(ativosContainer, ativosUrl, options)  
        new window.tableau.Viz(recuperadosContainer, recuperadosUrl, options)  
        new window.tableau.Viz(obitosContainer, obitosUrl, options)  
      } 

    render(){
        return(
            <Layout>
                <SEO title="COVID-19 - Nova Friburgo" />
                <PageTransition>
                    <BackgroundEspecial className="hero" subtitle="por Leonardo Muzi - dados atÃ© 22/06/2020"/>
                    <div ref={(div) => { this.casosContainer = div }}/> 
                    <div ref={(div) => { this.ativosContainer = div }}/> 
                    <div ref={(div) => { this.recuperadosContainer = div }}/> 
                    <div ref={(div) => { this.obitosContainer = div }}/> 
                </PageTransition>
            </Layout>
        )
    }
}

export default CovidPage
